import React from 'react';
import PropTypes from 'prop-types';

const Status = ({ usersAmount }) => {
  const formatStatus = () => {
    if (usersAmount === 0) {
      return <span className="badge bg-danger">    </span>;
    }
    if (usersAmount === 1) {
      return (
        <span className="badge bg-primary">{`${usersAmount}     `}</span>
      );
    }
    if (usersAmount > 1 && usersAmount < 5) {
      return (
        <span className="badge bg-primary">{`${usersAmount}     `}</span>
      );
    }
    return (
      <span className="badge bg-primary">{`${usersAmount}     `}</span>
    );
  };

  return <h2>{formatStatus()}</h2>;
};

Status.propTypes = {
  usersAmount: PropTypes.number.isRequired,
};

export default Status;
